import React, { useState, useEffect } from 'react';
import { Panel, PanelHeader, Title, Card, Div } from '@vkontakte/vkui';


import './styles/Results.css';
import * as test_result_text from './Test data/test_1.json';

const Result_1 = props => {
  const test_id = 1;

  let result_text;

  if (props.result >= 49) {
    result_text = test_result_text.resulttext.res49_60;
  } else if (props.result >= 37) {
    result_text = test_result_text.resulttext.res37_48;
  } else if (props.result >= 25) {
    result_text = test_result_text.resulttext.res25_36;
  } else if (props.result >= 13) {
    result_text = test_result_text.resulttext.res13_24;
  } else {
    result_text = test_result_text.resulttext.res0_12;
  }

  return (
    <Panel id={props.id}>
      <PanelHeader>
        Тест {test_id}
      </PanelHeader>
      <Div>
        <Card mode='shadow' className='Card'>
          <Title level="1" weight="heavy" style={{ marginBottom: 16}} className='ResultText'>Ваш результат</Title>
          <Title level="1" weight="heavy" className='ResultText'>{props.result}</Title>
        </Card>
      </Div>
      <Div>
        <Card mode='shadow' className='Card'>
          {result_text}
        </Card>
      </Div>
    </Panel>
  );
}


export { Result_1 };